body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  margin: 0;
  padding: 0;
  
}

body{
    font-family: 'Gentium Plus', serif;
   
}
.patch{
    background-image: url("https://cdn.pixabay.com/photo/2016/01/30/23/29/wood-floor-1170743_1280.jpg");
}
.container{
  width:100%;
  display: flex;
  flex-flow: row;
  justify-content: center;
  padding:0 20px;
  background-color: black;
}

.container li{
  display: inline-block;
  list-style: none ;
  margin:0px 25px;
  padding: 20px 10px 15px 10px;
  font-size: large;  
  transition: background-color 2s ease;
}

.container a{
  color: #fff;
}

a{
  text-decoration: none;
}

.product-container{
    margin: 64px 96px 0px 96px;
    padding: 4px 4px;
    border-bottom: 2px solid #808080 ;
}
.heading{
    width: 200px;
    /* border-radius: 5px; */
    /* box-shadow:2px 4px 5px #736653;
    border-bottom: 2px solid rgb(128, 165, 234);
    border-right: 2px solid rgb(211, 128, 234); */
}
.product-content{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    cursor:default;
}
.category{
    height: 300px;
    width: 250px;
    margin: 40px 20px;
    border-radius: 15px;
    /* color: #A18A68; */
    background-color:rgba(187, 219, 250, 0.5    );
    display: flex;
    flex-flow: column;
    text-align:center;
    font-weight: lighter;
    /* box-shadow: 2px 4px 5px ; */
    transition: all 0.2s ease;
}
.category img{
    height: 250px;
    width: 250px;
    /* border-radius: 15px;   */
}
 .category h3{
    margin-top: 5px;
 }
 .category:hover{
    /* filter: brightness(0.85); */
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    /* box-shadow: 6px 6px 5px ; */
 }

footer{
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #707070;
    display:flex;
    flex-flow: row;
    justify-content: space-evenly;
    height:230px;
    text-align: center;
    padding:40px;
}
.menu ul{
    margin-top: 10px;
    list-style: none;
    line-height: 30px;
    font-size: 89%;
}
i{
    margin: 10px;
}
.upper{
    padding: 20px 20px;
    display: inline-block;
    /* border-top: 1px solid rgb(193, 180, 160); */
    color: rgb(210, 196, 196);
}
.innBox{
    width: 50%;
    margin-top: 100px;
    margin-left: 50px;
    margin-bottom: 70px;
    background-color: rgba(255, 255, 255, 0.1);
    padding: 10px 10px;
    border-radius: 15px 15px;
    
}
.innBox p{
    font-weight: lighter;
}
.innBox h1{
    text-align: center;
}

.sliderimg{
    width: 100%;
    height: 500px;
    object-fit: cover;
    border-radius: 15px;
  }
.contain{
  padding: 15px 15px;
  border-radius: 15px;
  }
.description{
  position: relative;
}
.bottom-right {
  position: absolute;
  bottom: 116px;
  right: 136px;
  font-size:larger;
  background-color: rgba(250, 253, 255,0.5);
  padding: 40px 40px;
  border-radius: 15px;
}
.sober{
    height: 40px;
    display: inline-flex;
    justify-content:space-between;
    /* align-items: center;  */
    width:100%;
    border-bottom: solid lightgray 1px;  
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.sober h2{font-weight: 400;}
.sober ul{
    display:flex;
    flex-flow: row;
    list-style: none;
    
}
.sober li{
    margin:0px 60px;
    padding: 5px 5px;
}

.sober a
{   
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    color:black
}
.whole{
    padding: 20px 60px;
}

.collection-preview {
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
    padding:20px 80px;

}
.title {
  font-size: 28px;
  margin-bottom: 25px;
}
.row-items{
  display: flex;
}

.items-box {
  display: flex;
  flex-wrap: wrap;
  justify-content:space-between;
  
}
  
.collection-item {
    /* width: 15%; */
    min-width: 200px;
    display: flex;
    flex-direction: column;
    height: 350px;
    align-items: center;
    margin:20px 20px;
  }

.image {
  width: 100%;
  height: 95%;
  background-size: cover;
  background-position: center;
  margin-bottom: 5px;
}

.collection-footer {
  width: 100%;
  height: 5%;
  display: flex;
  justify-content: space-between;
  font-size: 18px;
}
.name {
  width: 90%;
  margin-bottom: 15px;
}

.price {
  width: 10%;
}



