.collection-item {
    /* width: 15%; */
    min-width: 200px;
    display: flex;
    flex-direction: column;
    height: 350px;
    align-items: center;
    margin:20px 20px;
  }

.image {
  width: 100%;
  height: 95%;
  background-size: cover;
  background-position: center;
  margin-bottom: 5px;
}

.collection-footer {
  width: 100%;
  height: 5%;
  display: flex;
  justify-content: space-between;
  font-size: 18px;
}
.name {
  width: 90%;
  margin-bottom: 15px;
}

.price {
  width: 10%;
}


